export default {
	data() {
		return {
			disabled: false,
			money: '',
			list: '',
			type: ''
		};
	},
	components: {},
	computed: {
		free() {
			return !this.money ? '0.00' : (this.money * (Number(this.list.tax_point) / 100) + Number(this.list.tixian_fee)).toFixed(2)
		}
	},
	methods: {
		submit() {
			this.$store.dispatch('setWithdraw', {
				amount: this.money,
			}).then(res => {
				this.disabled = false
				uni.showToast({
					title: res.message,
					success: (res) => {
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 1000)
					}
				})
			}).catch(err => {
				this.disabled = false
			})
		}
	},
	onLoad(e) {
		this.type = e.type
		this.$store.dispatch('getCashView', {
		}).then(res => {
			this.list = res.data
		}).catch(err => {})
	}
}
